import GameObject from '../GameObject';
import ImageRepository from '../../Utils/ImageRepository';

class Pike extends GameObject{
    constructor() {
        super();
        this.spriteoffset = {'x':0, 'y':32};
        this.hitbox={
            top:16,
            left:0,
            height:16,
            width:32,
        };
    }

    load(){
        this.sprite = ImageRepository.get('img/items.png');
    }

    gothru(character, direction){
        character.die();
    }
}

export default Pike;


