import Bullet from './Object/Bullet';
import Particle from './Object/Particle';
import Clone from './Object/Clone';
import Pike from './Object/Pike';
import Laser from './Object/Laser';
import Timer from './Object/Timer';
import Switch from './Object/Switch';
import FlyingThing from './Object/FlyingThing';
import FlyingText from './Object/FlyingText';
import Gem from './Object/Gem';
import Platform from './Object/Platform';
import Door from './Object/Door';
import Text from './Object/Text';
import GraphicComponent from '../Utils/GraphicComponent';
import levels from '../../data/levels';

class ObjectCreator {
  constructor() {
    this.seqValue = 2;
  }

  seqNext() {
      return this.seqValue++;
  }

  createObjet(proto, x, y) {
    let objet, maxlevel;

    switch (proto.type){
      case 'bullet':
        objet = new Bullet(proto);
        break;
      case 'particle':
        objet = new Particle(proto);
        break;
      case 'clone':
        objet = new Clone(proto);
        break;
      case 'pike':
        objet = new Pike();
        break;
      case 'laser':
        objet = new Laser(proto);
        break;
      case 'timer':
        objet = new Timer(proto);
        break;
      case 'switch':
        objet = new Switch(proto);
        break;
      case 'flyingthing':
        objet = new FlyingThing(proto);
        break;
      case 'flyingtext':
        objet = new FlyingText(proto);
        break;
      case 'gem':
        objet = new Gem(proto);
        break;
      case 'platform':
        objet = new Platform(proto);
        break;
      case 'door':
        maxlevel = localStorage.getItem('maxlevel');
        objet = new Door(proto);
        break;
      case 'text':
        objet = new Text(proto);
        break;
    }

    objet.id = this.seqNext();
    objet.coords.x = x;
    objet.coords.y = y;
    objet.load();
    objet.graphicComponent = new GraphicComponent(objet);
    window.env.game.current_screen.objects.push(objet);
    return objet;
  }


  createBackgroundObjet(objet, x, y) {
    var id = this.seqNext();
    objet.id = id;
    objet.coords.x = x * window.env.gui.display.tilesize;
    objet.coords.y = y * window.env.gui.display.tilesize;
    objet.ingrid   = {x:x, y:y};
    objet.load();
    objet.graphicComponent = new GraphicComponent(objet);
    window.env.game.current_screen.grid[x][y].push(objet);
  }
}

var instance = new ObjectCreator();

export default instance;


