import GameObject from '../GameObject';
import ImageRepository from '../../Utils/ImageRepository';
import ObjectCreator from '../ObjectCreator';

class Laser extends GameObject{
    constructor(proto) {
        super();
        this.spriteoffset = {'x':64, 'y':32};
        this.hitbox={
            top:0,
            left:13,
            height:32,
            width:6,
        };
        this.enabled = true;

        if (proto.trigger == undefined) {
          proto.trigger = {
            type: 'timer',
            delay: 500,
            coords: this.coords
          }
        }

        proto.trigger.callback = this.toggle.bind(this);

        this.trigger = ObjectCreator.createObjet(proto.trigger, proto.trigger.coords.x * 32, proto.trigger.coords.y * 32);
    }

    load(){
        this.sprite = ImageRepository.get('img/items.png');
    }

    enable(){
      this.enabled = true;
      this.spriteoffset.x = 64;
    }

    disable(){
      this.enabled = false;
      this.spriteoffset.x = 32;
    }

    toggle(){
      if (this.enabled) {
        this.disable();
      } else {
        this.enable();
      }
    }

    gothru(character, direction){
        if (this.enabled){
            character.movement.y *= -0.5;
            character.movement.x *= -1;
            character.loselife();
        }
    }
};

export default Laser;


