import GameObject from '../GameObject';
import ImageRepository from '../../Utils/ImageRepository';
import Block from './Block';
import levels from '../../../data/levels';

class Background extends Block {
    constructor() {
        super();

        this.type = 'background';
        this.isbackground = true;
        this.spriteoffset = {'x':0, 'y':32};        
    }

    load() {
        var src;
        if (levels[window.env.game.current_screen.number].tileset){
            src='img/'+levels[window.env.game.current_screen.number].tileset+'.png';
        }
        else{
            src='img/scifi.png';
        }
        this.sprite = ImageRepository.get(src);
    }

    gothru(character) {
        return true;
    }

    beforedraw() {
        var rand = Math.random();
        if (levels[window.env.game.current_screen.number].tileset){
            if (rand > 0.98){
                this.spriteoffset.x = 32;
            }
            else{
                this.spriteoffset.x = 0;
            }
        }
        else{
            if (rand > 0.9){
                this.spriteoffset.x = 32;
            }
            else if (rand > 0.88){
                this.spriteoffset.x = 64;
            }
            else{
                this.spriteoffset.x = 0;
            }
        }
    }

    draw(context) {
        var display_x = this.coords.x;
        var display_y = this.coords.y;
        context.drawImage(this.sprite, this.spriteoffset.x, this.spriteoffset.y, this.spritesize.x, this.spritesize.y, display_x, display_y, this.spritesize.x, this.spritesize.y);
    }
}

export default Background;


