import GameObject from '../GameObject';
import ImageRepository from '../../Utils/ImageRepository';

class Switch extends GameObject{
  constructor(proto) {
        super();
    this.spriteoffset = {'x':32, 'y':64};
    this.hitbox={
      top:16,
      left:8,
      height:16,
      width:16,
    };

    this.coords = proto.coords;
    this.fall = false;
    this.callback = proto.callback;
  }

  move(){};

  load(){
      this.sprite = ImageRepository.get('img/items.png');
  }


  enter(){
    console.log('enter');
    this.spriteoffset.x = 64;
    this.callback();
  }

  exit(){
    this.spriteoffset.x = 32;
    console.log('exit');
    this.callback();
  }
}

export default Switch;


