import KeyBoardManager from '../../Utils/Input/KeyBoardManager';
import Menu from '../Menu';
import menus from '../../../data/menus'

/*
 *  Submenu for controls configuration
 */
var ControlMenu = function(playerNumber){
  this.playerNumber = playerNumber;
  this.title = 'Controls Player ' + (playerNumber + 1);
  var thatControlMenu = this;
  this.items = [
    {
      title: 'Up key',
      callback: function(){
        this.title = 'Up key: _';
        KeyBoardManager.reset();
        this.takeFocus();
      },
      onLoad: function(){
        this.title = 'Up key: '+KeyBoardManager.keys[window.env.config.values.players[thatControlMenu.playerNumber].keys.up];
      },
      onAction: function(key){
        if (KeyBoardManager.keys[key] != undefined){
          this.title = 'Up key: '+KeyBoardManager.keys[key];
          window.env.config.values.players[thatControlMenu.playerNumber].keys.up = key;
          this.removeFocus();
        }
      }
    },
    {
      title: 'Down key',
      callback: function(){
        this.title = 'Down key: _';
        KeyBoardManager.reset();
        this.takeFocus();
      },
      onLoad: function(){
        this.title = 'Down key: '+KeyBoardManager.keys[window.env.config.values.players[thatControlMenu.playerNumber].keys.down];
      },
      onAction: function(key){
        if (KeyBoardManager.keys[key] != undefined){
          this.title = 'Down key: '+KeyBoardManager.keys[key];
          window.env.config.values.players[thatControlMenu.playerNumber].keys.down = key;
          this.removeFocus();
        }
      }
    },
    {
      title: 'Left key',
      callback: function(){
        this.title = 'Left key: _';
        KeyBoardManager.reset();
        this.takeFocus();
      },
      onLoad: function(){
        this.title = 'Left key: '+KeyBoardManager.keys[window.env.config.values.players[thatControlMenu.playerNumber].keys.left];
      },
      onAction: function(key){
        if (KeyBoardManager.keys[key] != undefined){
          this.title = 'Left key: '+KeyBoardManager.keys[key];
          window.env.config.values.players[thatControlMenu.playerNumber].keys.left = key;
          this.removeFocus();
        }
      }
    },
    {
      title: 'Right key',
      callback: function(){
        this.title = 'Right key: _';
        KeyBoardManager.reset();
        this.takeFocus();
      },
      onLoad: function(){
        this.title = 'Right key: '+KeyBoardManager.keys[window.env.config.values.players[thatControlMenu.playerNumber].keys.right];
      },
      onAction: function(key){
        if (KeyBoardManager.keys[key] != undefined){
          this.title = 'Right key: '+KeyBoardManager.keys[key];
          window.env.config.values.players[thatControlMenu.playerNumber].keys.right = key;
          this.removeFocus();
        }
      }
    },
    {
      title: 'Save Changes',
      callback: function(){
        window.env.config.save();
        window.env.game.current_screen.stop();
        window.env.game.current_screen = new Menu();
        window.env.game.current_screen.load(menus['options']);
        window.env.game.current_screen.start();
      }
    },
    {
      title: 'Cancel',
      callback: function(){
        window.env.config.load();
        window.env.game.current_screen.stop();
        window.env.game.current_screen = new Menu();
        window.env.game.current_screen.load(menus['options']);
        window.env.game.current_screen.start();
      }
    },
  ];
}

export default ControlMenu;


